import useExchangeRate from '../hooks/useExchangeRate';

export default function ExchangeRate() {
  const { isLoading, data: exchangeRate } = useExchangeRate();

  return (
    <>
      <h1></h1>
      {isLoading
        ? <p> </p>
        : <p>{exchangeRate.toFixed(2)}</p>}
    </>
  );
}
